  import wx from 'weixin-js-sdk'
  import empty from '../components/empty'
import {mapGetters,mapActions} from 'vuex'
export default {
  data(){
    return{
      dataList:[],
      flag:false
    }
  },
  created(){
    // localStorage.setItem('token','Bearer eyJhbGciOiJIUzUxMiJ9.eyJzdWIiOiIxNzc1NTc1NTY3NiIsInNjb3BlcyI6WyJVU0VSIl0sImlzcyI6Imh0dHA6Ly95dWFvcS5jb20iLCJpYXQiOjE1MjY4ODcwMzksImV4cCI6MTU0NDg4NzAzOX0.JP90fTfrOmDqlVHHyTKJg8WP9TB-O-wdTXX6Xd41xkFJhYLguQf3jUVcY4cvc_d5O0nCBf3laQLtS0AOWjcl8w')
    if(this.$route.query.token){
      localStorage.setItem('token',decodeURIComponent(this.$route.query.token))
    }
    if(this.$route.query.refreshToken){
      localStorage.setItem('refreshToken',decodeURIComponent(this.$route.query.refreshToken))
    }
    this.showLoading()
    this.returnList({this:this,data:{viewIndex:0,viewSize:10},method:this.returnList})
  },
  computed:{
  },
  components:{
    empty
  },
  methods:{
    ...mapActions(['returnList']),
    goToIndex(){
      wx.miniProgram.switchTab({
        url: '/pages/home/index'
      })
    },
    toSe(list){
      console.log(list)
      // wx.miniProgram.navigateTo({
      //   url:'/pages/webview/index?path=reviewProgress&orderId='+list.orderId+'&returnId='+list.returnId
      // })
     this.$router.push('/reviewProgress?returnId='+list.returnId+'&id='+list.orderId)
    },
    getData(data){
     this.flag = true
    this.dataList = data
    },
    choseWhy(k){
      console.log(k);
    }
  },
  mounted(){
    document.title = '退换售后'
  },
  filters:{
    formTime(c){
      if(!c){
        return ''
      }
      let time =  new Date(c)
      let Y= time.getFullYear()
      let M= time.getMonth() +1 <10? '0'+ (time.getMonth()+1):time.getMonth() +1
      let D= time.getDate()
      let h= time.getHours()
      let m= time.getMinutes()
      let s= time.getSeconds()
      return Y+'-'+M+'-'+D+' '+h+':'+m+':'+s
    },
    fiState(s){
      let d=''
      switch (s){
        case 'RETURN_WAITEXAMINE':
          d= '待审核'
        break;
        case 'RETURN_WAITFEFUND':
          d= '待退款'
          break;
        case 'RETURN_COMPLETED':
          d= '已完成'
          break;
        case 'RETURN_REJECTAPPLY':
          d= '已拒绝'
          break;
      }
      return d
    }
  },
   beforeRouteLeave(to,from,next){
     console.log(to)
       if(to.name==='reviewProgress'){
         next()
       }
       else{
         wx.miniProgram.switchTab({
         url:'/pages/me/index'
         })
       }
   }
}



